'use strict';

import moment = require("moment");


/**
* Formats a given Date in long format (for Header in journal pages)
*/
export function formatDate(date: Date, template: string, locale: string): string {
    moment.locale(locale);
    let now = moment(date).format(template);
    /*

    let dateFormatOptions: Intl.DateTimeFormatOptions = {
        weekday: "long",
        year: "numeric",
        month: "long",
        day: "numeric"
    };
    return date.toLocaleDateString(locale, dateFormatOptions);
    */
    return now;
}




/**
 * Return day of week for given string. 
 * 
 * Update: Using momentjs to support locales (since first day of week differs internationally)
 */
 export function getDayOfWeekForString(day: string, locale: string): number {
    // Support for English, German, French, Spanish, Italian, Portuguese, Dutch, Russian, Chinese (Pinyin), Japanese (Romaji), and Arabic
    if (day.match(/monday|mon|mo|montag|lun|lundi|lunes|luned|segunda-feira|seg|maandag|ma|||xngq y|getsuybi|/i)) { return 1; }
    if (day.match(/tuesday|tue|tu|dienstag|die|mar|mardi|martes|marted|tera-feira|ter|dinsdag|di|||xngq r|kaybi|/i)) { return 2; }
    if (day.match(/wednesday|wed|we|mittwoch|mit|mer|mercredi|mircoles|mi|mercoled|quarta-feira|qua|woensdag|woe|||xngq sn|suiybi|/i)) { return 3; }
    if (day.match(/thursday|thu|th|donnerstag|don|jeu|jeudi|jue|jueves|gioved|quinta-feira|qui|donderdag|do|||xngq s|mokuybi|/i)) { return 4; }
    if (day.match(/friday|fri|fr|freitag|fre|ven|vendredi|vie|viernes|venerd|sexta-feira|sex|vrijdag|vr|||xngq w|kin'ybi|/i)) { return 5; }
    if (day.match(/saturday|sat|sa|samstag|sam|samedi|sb|sbado|sabato|sbado|zaterdag|za|||xngq li|doybi|/i)) { return 6; }
    if (day.match(/sunday|sun|su|sonntag|dim|dimanche|dom|domingo|domenica|domingo|zondag|zo|||xngq r|nichiybi|/i)) { return 7; }
    return -1;
    
}

/**
 * Return day of week for given string. 
 * 
 * Update: Using momentjs to support locales (since first day of week differs internationally)
 */
 export function getMonthForString(month: string): number {
    // Support for English, German, French, Spanish, Italian, Portuguese, Dutch, Russian, Chinese (Pinyin), Japanese (Romaji), and Arabic months
    if (month.match(/jan|january|januar|janvier|enero|gennaio|janeiro|januari|||yyu|ichigatsu|/i)) { return 0; }
    if (month.match(/feb|february|februar|fvrier|febrero|febbraio|fevereiro|februari|||ryu|nigatsu|/i)) { return 1; }
    if (month.match(/mar|march|mrz|mars|marzo|maart||snyu|sangatsu|/i)) { return 2; }
    if (month.match(/apr|april|avril|abril|aprile|april|||syu|shigatsu|/i)) { return 3; }
    if (month.match(/may|mai|mayo|maggio|mei||wyu|gogatsu|/i)) { return 4; }
    if (month.match(/jun|june|juin|junio|giugno|junho|juni||liyu|rokugatsu|/i)) { return 5; }
    if (month.match(/jul|july|juli|juillet|julio|luglio|julho||qyu|shichigatsu|/i)) { return 6; }
    if (month.match(/aug|august|aot|agosto|agosto|augustus|||byu|hachigatsu|/i)) { return 7; }
    if (month.match(/sep|sept|september|septembre|septiembre|settembre|setembro|september|||jiyu|kugatsu|/i)) { return 8; }
    if (month.match(/oct|october|oktober|octobre|octubre|ottobre|outubro|oktober|||shyu|jugatsu|/i)) { return 9; }
    if (month.match(/nov|november|novembre|noviembre|novembro|||shyyu|juichigatsu|/i)) { return 10; }
    if (month.match(/dec|december|dezember|dcembre|diciembre|dicembre|dezembro||sh'ryu|juunigatsu|/i)) { return 11; }

    return -1;
}


   /**
     * Checks whether any embedded expressions with date formats are in the template, and replaces them in the value using the given date. 
     * 
     * @param st
     * @param date 
     */
    // https://regex101.com/r/i5MUpx/1/
    // private regExpDateFormats: RegExp = new RegExp(/\$\{(?:(year|month|day|localTime|localDate|weekday)|(d:\w+))\}/g);
    // fix for #52
    // private regExpDateFormats: RegExp = new RegExp(/\$\{(?:(year|month|day|localTime|localDate|weekday)|(d:\w+))\}/g);
    const regExpDateFormats: RegExp = new RegExp(/\$\{(?:(year|month|day|localTime|localDate|weekday)|(d:[\s\S]+?))\}/g);

    export function replaceDateFormats(template: string, date: Date, locale?: string): string {
        let matches : RegExpMatchArray | null = template.match(regExpDateFormats);
        if(matches === null) {
            return template; 
        }

        let mom: moment.Moment = moment(date);
        moment.locale(locale);

        matches.forEach(match => {
            switch (match) {
                case "${year}":
                    template = template.replace(match, mom.format("YYYY")); break;
                case "${month}":
                    template = template.replace(match, mom.format("MM")); break;
                case "${day}":
                    template = template.replace(match, mom.format("DD")); break;
                case "${localTime}":
                    template = template.replace(match, mom.format("LT")); break;
                case "${localDate}":
                    template = template.replace(match, mom.format("LL")); break;
                case "${weekday}":
                    template = template.replace(match, mom.format("dddd")); break;
                case "${week}":
                    template = template.replace(match, mom.week() + ""); break;
                default:
                    // check if custom format
                    if (match.startsWith("${d:")) {

                        let modifier = match.substring(match.indexOf("d:") + 2, match.length - 1); // includes } at the end
                        // st.template = st.template.replace(match, mom.format(modifier));
                        // fix for #51
                        template = template.replace(match, mom.format(modifier));
                        break;
                    }
                    break;
            }
        });

        return template;
    }

    export function replaceDateTemplatesWithMomentsFormats(template: string): string {
        let matches: RegExpMatchArray | null = template.match(regExpDateFormats);
        if (matches === null) {
            return template;
        }

        matches.forEach(match => {
            switch (match) {
                case "${year}":
                    template = template.replace(match, "YYYY"); break;
                case "${month}":
                    template = template.replace(match, "MM"); break;
                case "${day}":
                    template = template.replace(match, "DD"); break;
                case "${localTime}":
                    template = template.replace(match, "LT"); break;
                case "${localDate}":
                    template = template.replace(match, "LL"); break;
                case "${weekday}":
                    template = template.replace(match, "dddd"); break;
                default:
                    // check if custom format
                    if (match.startsWith("${d:")) {

                        let modifier = match.substring(match.indexOf("d:") + 2, match.length - 1); // includes } at the end
                        // st.template = st.template.replace(match, mom.format(modifier));
                        // fix for #51
                        template = template.replace(match, modifier);
                        break;
                    }
                    break;
            }
        });
        return template;

    }